		// Action For LOGIN PAGE
		POST_LOGIN({commit, dispatch}, ConvertFormLogin){
			axios.post('https://b2ctest.id.dyned.com/api/v1/jwt/token-request', ConvertFormLogin)
			.then(res => {
				//console.log(res)
				commit('authUser',{
					ApiToken: res.data.token
				}),
				commit('loginStatus',{
					Message: 'login Success'
				})
				dispatch('LOGIN_SUCCSESSFUL')
			})
			.catch(err => {
				console.log(err)
				commit('authError',{
					Error401: true
				}),
				commit('loginStatus',{
					Message: 'login Fail'
				})
				dispatch('LOGIN_FAILED')
			})
		},

		LOGIN_SUCCSESSFUL({commit, state}){
			localStorage.token = state.ApiToken
			delete localStorage.Error401

			// TO GET USER PROFILE FOR HEADER
			axios.get('/profile',{
				headers: {
					Authorization: localStorage.getItem('token')
				}
			})
			.then(res =>{
				commit('storeDataAdditional', res.data)

					// TO GET USER ROLE
				axios.get('/acl', {
					headers: {
					Authorization: localStorage.getItem('token')
				}
				})
				.then(res => {
					commit('commitFiveParamLogin', res)
					commit('roleId', {
						UserRoleId: res.data.live.roles[0]
					})
					if (state.DataAdditional.timezone == null) {
						router.push('/timezone')
					} else {
						if (state.UserRoleId == "coach") {
							router.push('/coach')
						} else if (state.UserRoleId == "student") {
							router.push('/student')
						}
					}
				})
					.catch(err => {
					console.log(err)
				})
			})
				.catch(err => {
				console.log(err)
				if(err.response.message ="Your Account has expired"){
					commit('loginStatus',{
						Message: 'Account expired'
					})
					commit('authError',{
						Error401: true
					})
					commit('authUser',{
						ApiToken: null
					})
				}
			})
		},

		LOGIN_FAILED({state}){
			localStorage.Error401 = state.Error401
			delete localStorage.token
			delete localStorage.vuex
		},
		LOGOUT ({commit}) {
			localStorage.removeItem('token')
			localStorage.removeItem('expirationDate')
			delete localStorage.vuex
			commit('clearAuthData')
			router.replace('/login')
		},
		LOGOUT_EXP ({commit}) {
			commit('clearAuthData')
			delete localStorage.vuex
			localStorage.removeItem('token')
			router.replace('/login')
		},
